@charset "UTF-8";

/* 
Стиль React
BlockName-ElemName_modName_modVal

Имена записываются латиницей.

Имена блоков и элементов пишутся с заглавной буквы. Имена модификаторов — со строчной.

Каждое слово внутри имени пишется с заглавной буквы.

Имя элемента отделяется от имени блока одним дефисом (-).

Разделители имени и значения модификаторов совпадают с классической схемой.
*/

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf");
  font-weight: 400;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.ttf");
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.ttf");
  font-weight: 700;
}

body {
  font-family: "Roboto", serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: #fcfbf7;
}

body.theme_light {
  color: #261c00;
}

.fontColor_gray {
  color: #918f8a;
}

.theme_light .fontColor_gray {
  color: #bfbfbf;
}

.fontType_headline {
  font-weight: bold;
  font-size: 30px;
  line-height: 32px;
}

.fontType_subhead {
  font-weight: bold;
  font-size: 20px;
  line-height: 22px;
}

.fontType_caption {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}

.Person-AvatarWrapper {
  display: block;
}

.Person-Avatar {
  border-radius: 50%;
  filter: grayscale(1);
}

.Person-Name {
  text-overflow: ellipsis;
  overflow: hidden;
}

.Person-Value {
  margin-top: 4px;
}

.Person_ordinary {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 104px;
  min-height: 142px;
  box-sizing: border-box;
  padding-top: 20px;
  border-radius: 6px;
}

.Person_ordinary_hoverOn:hover {
  background: radial-gradient(85.62% 148.33% at 49.85% 100%, rgba(68, 58, 42, 0.7864) 0%, rgba(0, 0, 0, 0.8) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(255, 255, 255, 0.1);
}

.Person_ordinary_hoverOn.Person_ordinary_selected {
  background: radial-gradient(91.67% 122.17% at 69.17% -11.17%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0px 6px 15px rgba(255, 162, 0, 0.4);
}

.Person_ordinary .Person-AvatarWrapper {
  position: relative;
}

.Person_ordinary .Person-AvatarWrapper::before {
  display: block;
  content: attr(data-emoji);
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translate(-50%, -100%);
  font-size: 30px;
  line-height: 32px;
  z-index: 5;
}

.Person_ordinary .Person-Name {
  max-width: 100px;
  text-align: center;
  margin-top: 2px;
}

.Person_horizontal {
  display: grid;
  grid-template-columns: 40px 182px;
  grid-template-rows: 40px;
  column-gap: 14px;
  padding-left: 16px;
}

.Person_horizontal .Person-Avatar {
  width: 40px;
}

.Person_horizontal .Person-Info {
  display: flex;
  flex-direction: column;
  padding-top: 1px;
}

.theme_light .Person-Avatar {
  filter: none;
}

.theme_light .Person_ordinary_hoverOn:hover {
  background: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%, rgba(234, 234, 234, 0) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(207, 207, 207, 0.5);
}

.theme_light .Person_ordinary_hoverOn.Person_ordinary_selected {
  background: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.8);
}

.HeaderWrapper {
  margin-top: 20px;
}

.Header {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.Header-Title {
  margin-bottom: 8px;
}

.Divider {
  background: #918f8a;
  opacity: 0.3;
}

.Divider_vertical {
  height: 100%;
  width: 2px;
}

.Divider_horizontal {
  height: 2px;
  width: 100%;
}

.theme_light .Divider {
  background: #bfbfbf;
}

.PageBodyWrapper {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.LeadersBody {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.LeadersBody-Pedestals {
  display: flex;
  align-items: flex-end;
}

.LeadersBody-Pedestal_1 {
  z-index: 5;
}

.LeadersBody-Pedestal_2,
.LeadersBody-Pedestal_3 {
  z-index: 3;
  margin-right: -16px;
  margin-left: -16px;
}

.LeadersBody-Pedestal_3 .LeadersBody-PersonWrapper,
.LeadersBody-Pedestal_3 .LeadersBody-PlaceWrapper,
.LeadersBody-Pedestal_5 .LeadersBody-PersonWrapper,
.LeadersBody-Pedestal_5 .LeadersBody-PlaceWrapper {
  padding-right: 16px;
}

.LeadersBody-Pedestal_2 .LeadersBody-PersonWrapper,
.LeadersBody-Pedestal_2 .LeadersBody-PlaceWrapper,
.LeadersBody-Pedestal_4 .LeadersBody-PersonWrapper,
.LeadersBody-Pedestal_4 .LeadersBody-PlaceWrapper {
  padding-left: 16px;
}

.LeadersBody-PersonWrapper {
  margin-bottom: 16px;
}

.LeadersBody-Block {
  text-align: center;
  padding-top: 18px;
  height: 68px;
  width: 120px;
  box-sizing: border-box;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) -2.22%, rgba(0, 0, 0, 0.8) 100%), radial-gradient(149.08% 95.38% at 38.75% 71.48%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(103, 103, 103, 0.6);
}

.LeadersBody-Block_1 {
  height: 108px;
  background: radial-gradient(91.67% 122.17% at 69.17% -11.17%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0px 6px 15px rgba(255, 162, 0, 0.4);
}

.LeadersBody-Block_2,
.LeadersBody-Block_3 {
  height: 88px;
}

.theme_light .LeadersBody-Block {
  background: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%, rgba(234, 234, 234, 0) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(207, 207, 207, 0.5);
}

.theme_light .LeadersBody-Block_1 {
  background: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.8);
}

.VoteBody {
  flex-grow: 1;
  min-width: 607px;
  display: flex;
  align-items: center;
}

.VoteBody-PersonsGroup {
  display: grid;
  row-gap: 4px;
}

.VoteBody-PersonsGroup:nth-child(2) {
  margin-left: 32px;
  margin-right: 31px;
}

.VoteBody-PersonsGroup:nth-last-child(2) {
  margin-left: 31px;
  margin-right: 32px;
}

.VoteBody-ButtonsGroup {
  display: grid;
  row-gap: 56px;
}

.VoteBody-Button::before {
  display: block;
  content: url("/images/button-dark.svg");
  width: 64px;
  height: 64px;
  margin: 0 auto;
}

.VoteBody-Button_top::before {
  transform: rotate(180deg);
}

.VoteBody-Button:hover::before,
.VoteBody-Button_disabled::before {
  content: url("images/button-hover-dark.svg");
}

.theme_light .VoteBody-Button::before {
  content: url("/images/button-light.svg");
}

.theme_light .VoteBody-Button:hover::before,
.theme_light .VoteBody-Button_disabled::before {
  content: url("images/button-hover-light.svg");
}

.ChartBody {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ChartBody-InternalWrapper {
  width: 552px;
  padding-top: 4px;
}

.ChartBody-Columns {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 180px;
}

.ChartBody-Users {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 40px;
  padding-top: 24px;
}

.Column {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  height: 100%;
  width: 40px;
}

.Column-Title {
  text-align: center;
}

.Column-Value {
  position: absolute;
  left: 50%;
  transform: translate(-50%, calc(-100% - 8px));
}

.Column-Line {
  border-radius: 6px;
  min-height: 8px;
  max-height: 117px;
  margin-bottom: 12px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 100%), radial-gradient(149.08% 93.3% at 38.75% 72.1%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(103, 103, 103, 0.6);
}

.Column-Line_active {
  background: radial-gradient(81.14% 100% at 50% 0%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0px 6px 15px rgba(255, 162, 0, 0.5);
}

.theme_light .Column-Line {
  background: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%, rgba(234, 234, 234, 0) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(207, 207, 207, 0.5);
}

.theme_light .Column-Line_active {
  background: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.8);
}

.ActivityBody {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 425px;
}

.ActivityBody-Field {
  width: 100%;
  padding-top: 28px;
  box-sizing: border-box;
}

.ActivityBody-Row {
  display: flex;
}

.ActivityBody-Row:nth-child(odd) {
  justify-content: flex-start;
}

.ActivityBody-Row:nth-child(even) {
  justify-content: flex-end;
}

.ActivityBody-Turret {
  width: 34px;
  height: 13px;
  position: relative;
}

.ActivityBody-Turret::after {
  display: block;
  position: absolute;
  bottom: 0;
}

.ActivityBody-Turret_min::after {
  content: url("images/1x/min-dark.png");
}

.ActivityBody-Turret_mid::after {
  content: url("images/1x/mid-dark.png");
}

.ActivityBody-Turret_max::after {
  content: url("images/1x/max-dark.png");
}

.ActivityBody-Turret_extra::after {
  content: url("images/1x/extra-dark.png");
}

.ActivityBody-Intervals {
  display: grid;
  grid-template-columns: repeat(5, 60px);
  gap: 8px;
  margin-top: 28px;
}

.ActivityBody-Interval {
  width: 60px;
  text-align: center;
}

.ActivityBody-IntervalBlock {
  height: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ActivityBody-IntervalBlock div {
  border-radius: 2px;
  height: 100%;
  width: 100%;
}

.ActivityBody-IntervalBlock_sellSize div {
  position: relative;
  border-radius: 0;
  width: 52px;
  height: 4px;
  background: radial-gradient(51.52% 129.57% at 48.48% 50%, rgba(64, 59, 54, 0) 0%, rgba(19, 18, 17, 0.5) 100%);
  box-shadow: inset 0px -1px 2px rgba(255, 255, 255, 0.25), inset 3px 1px 16px rgba(112, 102, 94, 0.3);
}

.ActivityBody-IntervalBlock_sellSize div::before,
.ActivityBody-IntervalBlock_sellSize div::after {
  position: absolute;
  display: block;
  content: "";
  width: 4px;
  height: 12px;
  background: radial-gradient(150% 252.96% at 150% 50%, rgba(64, 59, 54, 0.65) 0%, rgba(0, 0, 0, 0.65) 100%);
  box-shadow: inset 0px -1px 2px rgba(255, 255, 255, 0.25), inset 3px 1px 16px rgba(112, 102, 94, 0.3);
  border-radius: 2px;
}

.ActivityBody-IntervalBlock_sellSize div::before {
  top: -4px;
  left: -4px;
}

.ActivityBody-IntervalBlock_sellSize div::after {
  top: -4px;
  right: -4px;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}

.ActivityBody-IntervalBlock_min div {
  background: radial-gradient(3906.1% 3815.36% at 89.06% 78.28%, rgba(19, 17, 16, 0.65) 0%, rgba(0, 0, 0, 0.65) 100%);
  box-shadow: inset -1px 0px 2px rgba(255, 255, 255, 0.2), inset 1px 1px 16px rgba(112, 102, 94, 0.2);
}

.ActivityBody-IntervalBlock_mid div {
  background: radial-gradient(5752.25% 5190.32% at 74.43% 60.32%, rgba(0, 0, 0, 0.9) 0%, rgba(35, 22, 0, 0.9) 0.01%, rgba(112, 92, 94, 0.9) 100%);
  box-shadow: inset -1px 0px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 10px rgba(93, 116, 141, 0.6);
}

.ActivityBody-IntervalBlock_max div {
  background: radial-gradient(5528.36% 3005.63% at 74.43% 75.84%, rgba(0, 0, 0, 0.9) 0%, rgba(33, 22, 2, 0.9) 0.01%, rgba(172, 113, 9, 0.9) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 10px rgba(242, 159, 13, 0.2);
}

.ActivityBody-IntervalBlock_extra div {
  background: radial-gradient(2258.03% 620.37% at 83.33% 88.95%, #201502 0%, #c7830a 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 10px rgba(242, 159, 13, 0.9);
}

.ActivityBody-IntervalValue {
  display: inline-block;
  margin-top: 8px;
}

.theme_light .ActivityBody-Turret_min::after {
  content: url("images/1x/min-light.png");
}

.theme_light .ActivityBody-Turret_mid::after {
  content: url("images/1x/mid-light.png");
}

.theme_light .ActivityBody-Turret_max::after {
  content: url("images/1x/max-light.png");
}

.theme_light .ActivityBody-Turret_extra::after {
  content: url("images/1x/extra-light.png");
}

.theme_light .ActivityBody-IntervalBlock_sellSize div {
  background: radial-gradient(2408.25% 2730.55% at 89.06% 78.28%, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.8) 100%);
  box-shadow: inset -1px 0px 2px rgba(255, 255, 255, 0.2), inset 1px 1px 16px rgba(103, 103, 103, 0.2);
}

.theme_light .ActivityBody-IntervalBlock_sellSize div::before,
.theme_light .ActivityBody-IntervalBlock_sellSize div::after {
  background: radial-gradient(2408.25% 2730.55% at 89.06% 78.28%, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.8) 100%);
  box-shadow: inset -1px 0px 2px rgba(255, 255, 255, 0.2), inset 1px 1px 16px rgba(103, 103, 103, 0.2);
}

.theme_light .ActivityBody-IntervalBlock_min div {
  background: radial-gradient(2408.25% 2730.55% at 89.06% 78.28%, rgba(250, 250, 250, 0.6) 0%, rgba(250, 250, 250, 0.6) 100%);
  box-shadow: inset -1px 0px 2px rgba(250, 250, 250, 0.2), inset 1px 1px 16px rgba(106, 106, 106, 0.2);
}

.theme_light .ActivityBody-IntervalBlock_mid div {
  background: radial-gradient(2408.25% 2730.55% at 89.06% 78.28%, rgba(250, 250, 250, 0.8) 0%, rgba(250, 250, 250, 0.8) 100%);
  box-shadow: inset -1px 0px 2px rgba(250, 250, 250, 0.2), inset 1px 1px 16px rgba(106, 106, 106, 0.3);
}

.theme_light .ActivityBody-IntervalBlock_max div {
  background: radial-gradient(68.1% 68.1% at 4.41% 31.9%, #fff6dd 8.72%, #fffefa 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.4);
}

.theme_light .ActivityBody-IntervalBlock_extra div {
  background: radial-gradient(66.02% 86.49% at -16.18% 13.51%, rgba(255, 186, 6, 0.85) 0%, #fff2ad 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.4);
}

.DiagramBody {
  flex-grow: 1;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.Legend {
  min-width: 304px;
  margin-left: 40px;
}

.LegendLine {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}

.LegendLine-DiffText {
  margin-right: 16px;
}

.LegendLine-Title {
  margin-left: 26px;
}

.LegendLine-Title::before {
  display: block;
  position: absolute;
  content: "";
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transform: matrix(1, 0, 0, -1, 0, 0);
}

.LegendLine-Title_0::before {
  background: radial-gradient(49.84% 49.84% at 49.84% 50.16%, rgba(255, 163, 0, 0.8) 71.88%, rgba(91, 58, 0, 0.8) 100%);
  box-shadow: 0px 0px 20px -8px rgba(248, 158, 0, 0.2), inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 162, 0, 0.9);
}

.LegendLine-Title_1::before {
  background: radial-gradient(49.84% 49.84% at 49.84% 50.16%, rgba(99, 63, 0, 0.5) 72.92%, rgba(15, 9, 0, 0.5) 100%);
  box-shadow: 0px 0px 20px -8px rgba(147, 93, 0, 0.2), inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(202, 129, 0, 0.9);
}

.LegendLine-Title_2::before {
  background: radial-gradient(100% 100% at 50% 0%, rgba(151, 151, 151, 0.5) 0%, rgba(41, 41, 41, 0.5) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5);
}

.LegendLine-Title_3::before {
  background: radial-gradient(100% 100% at 50% 0%, rgba(62, 62, 62, 0.5) 0%, rgba(40, 40, 40, 0.5) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5);
}

.Diagram {
  min-width: 240px;
  height: 240px;
  position: relative;
}

.Diagram-Img img {
  width: 240px;
  height: 240px;
}

.Diagram-Text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.Diagram-Text span {
  display: inline-block;
}

.Diagram-Text span:nth-child(1) {
  margin-bottom: 6px;
}

.theme_light .LegendLine-Title_0::before {
  background: radial-gradient(49.84% 49.84% at 49.84% 50.16%, rgba(255, 184, 0, 0.56) 81.25%, rgba(255, 239, 153, 0.32) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.9);
}

.theme_light .LegendLine-Title_1::before {
  background: radial-gradient(49.84% 49.84% at 49.84% 50.16%, rgba(255, 184, 0, 0.24) 81.25%, rgba(255, 239, 153, 0.12) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.4);
}

.theme_light .LegendLine-Title_2::before {
  background: radial-gradient(49.84% 49.84% at 49.84% 50.16%, rgba(166, 166, 166, 0.1725) 82.81%, rgba(203, 203, 203, 0.05) 92.19%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(105, 105, 105, 0.2);
}

.theme_light .LegendLine-Title_3::before {
  background: radial-gradient(49.84% 49.84% at 49.84% 50.16%, rgba(191, 191, 191, 0.345) 82.81%, rgba(228, 228, 228, 0.1) 92.19%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(131, 131, 131, 0.6);
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: radial-gradient(70.24% 70.24% at 49.87% 29.76%, rgba(19, 14, 0, 0.9) 0%, #000000 100%);
}

body.theme_light {
  background: #fff;
}

@media all and (orientation: portrait) {
  .HeaderWrapper {
    margin-left: 24px;
    margin-right: 24px;
  }

  .Header {
    display: block !important;
  }
}

@media (orientation: portrait) {
  .LeadersBody-Pedestal_4,
  .LeadersBody-Pedestal_5 {
    display: none;
  }

  .LeadersBody-Block_1 {
    height: 325px;
  }

  .LeadersBody-Block_2 {
    height: 275px;
  }

  .LeadersBody-Block_3 {
    height: 225px;
  }

  .VoteBody_landscape {
    display: none;
  }

  .VoteBody {
    min-width: 328px;
    justify-content: space-between;
  }

  .VoteBody-PersonsGroup {
    row-gap: 25px;
  }

  .VoteBody-PersonsGroup:nth-child(2),
  .VoteBody-PersonsGroup:nth-last-child(2) {
    margin: 0;
  }

  .VoteBody-ButtonsGroup {
    row-gap: 23px;
  }

  .VoteBody-ButtonsGroup .VoteBody-PersonsGroup {
    row-gap: 22px;
  }

  .DiagramBody {
    flex-direction: column;
    justify-content: center;
    padding-top: 7px;
  }

  .Diagram {
    min-width: 328px;
    min-height: 328px;
  }

  .Diagram-Img img {
    min-width: 328px;
    min-height: 328px;
  }

  .Diagram-Text_landscape {
    display: none;
  }

  .Diagram-Text span:nth-child(1) {
    margin-bottom: 8px;
  }

  .Legend {
    min-width: 328px;
    margin-top: 16px;
    margin-left: 0;
  }
