.HeaderWrapper {
  margin-top: 20px;
}
.Header {
  display: flex;
  align-items: center;
  flex-direction: column;
  &-Title {
    margin-bottom: 8px;
  }
}

@media all and (orientation: portrait) {
  .HeaderWrapper {
    margin-left: 24px;
    margin-right: 24px;
  }
  .Header {
    display: block !important;
  }
}
